play: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.question-text {\n    font-size: 20px;\n}\n\n.question-def-text {\n    font-size: 20px;\n    text-decoration: underline;\n    text-decoration-thickness: 3px;\n    text-decoration-color: yellow;\n}\n\n.question-def-text:hover {\n    background-color: var(--vscode-editor-lineHighlightBackground);\n    cursor: pointer;\n}\n\n.input-text {\n    color: white;\n    font-size: 15px;\n}\n\n.answer-text {\n    font-size: 15px;\n}\n\n.answer-text[data-correct=\"true\"] {\n    color: rgb(1, 198, 1);\n}\n\n.answer-text[data-correct=\"false\"] {\n    color: red;\n}\n\n/*\nQuiz Controls\n--------------------\n*/\n\n.controls-container {\n    width: 100%;\n    display: flex;\n    justify-content: center;\n    margin-top: 20px;\n    margin-bottom: 20px;\n}\n\n.controls-button {\n    padding: 5px 20px 5px 20px;\n    \n    border: none;\n    border-radius: 3px;\n\n    background-color: var(--vscode-button-background);\n    color: var(--vscode-button-foreground);\n    transition: all 0.5s;\n}\n\n.controls-button:hover { \n    background-color: var(--vscode-button-hoverBackground);\n    cursor: pointer;\n}\n\n/*\nQuiz Config\n------------------\n*/\n\n.quiz-config-container {\n    margin-top: 20px;\n    margin-bottom: 20px;\n}\n\n/* pretty checkbox switch color */\n.pretty.p-switch.p-fill input:checked~.state:before {\n    background-color: var(--vscode-button-background) !important;\n}", "",{"version":3,"sources":["webpack://./src/renderer/style.css"],"names":[],"mappings":"AAAA;;;CAGC;;AAED;IACI,aAAa;IACb,qBAAqB;AACzB;;AAEA;IACI,0BAA0B;;IAE1B,YAAY;;IAEZ,iDAAiD;IACjD,sCAAsC;IACtC,iCAAiC;AACrC;;AAEA;IACI,sDAAsD;IACtD,eAAe;AACnB;;AAEA;IACI,sDAAsD;IACtD,uDAAuD;AAC3D;;;AAGA;;;;;CAKC;AACD;IACI,gBAAgB;IAChB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;IACnB,eAAe;IACf,kBAAkB;IAClB,SAAS;IACT,2BAA2B;AAC/B;;AAEA;IACI,0B